/*reset start */
/*reset end */
/*清除默认样式*/
* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
img {
  border: none;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
/*清除dotted*/
a,
label {
  blr: expression(this.onFocus=this.blur());
}
a,
label {
  outline: none;
}
/*设置公用类*/
.fl {
  float: left;
  display: inline;
}
.fr {
  float: right;
  display: inline;
}
.p-r {
  position: relative;
}
.p-a {
  position: absolute;
}
.fix {
  position: fixed;
}
.hide {
  visibility: hidden;
}
.show {
  visibility: visible;
}
/*禁止复制*/
html,
body {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/*common start  */
.x-icon-back {
  background: url("../images/common/icon-back.png") center no-repeat;
  background-size: 100% 100%;
}
/*common end  */
.star-flex {
  /* Safari */
  display: -ms-flexbox;
  display: flex;
}
.app-wrapper {
  background-color: #f8f8f8;
  width: 100%;
  height: 100%;
}
.app-container {
  width: 100%;
  height: 100%;
}
.filter {
  -moz-filter: blur(3px);
  -ms-filter: blur(3px);
  filter: blur(3px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=10, MakeShadow=false);
  /* IE6~IE9 */
}



